@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&family=Open+Sans:wght@400;700&display=swap');

.home-page {
    display: flex;
    flex-direction: column;
    gap: 80px;
    min-height: 100vh;
}

.image-sm {
    height: 250px;
    width: 250px;
    border-radius: 200px;
    box-shadow: 0px 0px 20px rgba(255, 255, 0, 0.5); /* Light yellow shadow */
}

.inner-home {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.Hii-there-lg,
.name-lg,
.surname-lg,
.welcome-lg {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}

.Hii-there-lg {
    font-size: 6vh;
    color: #2c3e50; /* Dark grey */
}

.name-lg {
    font-size: 6vh;
    color: #3498db; /* Light blue */
}

.surname-lg {
    font-size: 6vh;
    color: #e67e22; /* Light orange */
}

.welcome-lg {
    font-size: 4vh;
    color: #34495e; /* Dark blue-grey */
}

.Portfolio-lg {
    color: #d35400; /* Light orange */
    font-family: 'Open Sans', sans-serif;
}

.mb-2 {
    margin-bottom: 2rem;
}

.mb-4 {
    margin-bottom: 4rem;
}

@media only screen and (max-width: 768px) {
    .home-page {
        gap: 40px;
    }

    .image-sm {
        align-self: flex-start;
        height: 180px;
        width: 180px;
        border-radius: 100px;
        background-color: #f1c40f; /* Light yellow */
    }

    .img-container {
        background-color: #f39c12; /* Light amber */
        padding: 10px;
        border-top-left-radius: 0px;
        border-top-right-radius: 100px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 100px;
        padding-left: 180px;
        align-self: flex-start;
    }

    .inner-home {
        flex-direction: column;
    }

    .Hii-there-lg {
        font-size: 4.5vh;
    }

    .name-lg {
        font-size: 4.5vh;
    }

    .surname-lg {
        font-size: 4.5vh;
    }

    .welcome-lg {
        font-size: 3vh;
    }

    .mb-2 {
        margin-bottom: 1.5rem;
    }

    .mb-4 {
        margin-bottom: 3rem;
    }
}

@media only screen and (max-width: 480px) {
    .home-page {
        gap: 20px;
    }

    .img-container {
        background-color: #008cff32; /* Light amber */
        box-shadow: 0 0 10px rgba(18, 161, 243, 0.84);
        border-top-left-radius: 0px;
        border-top-right-radius: 100px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 100px;
        margin-top: 25px;
        margin-bottom: 20px;
    }

    .image-sm {
        height: 100px;
        width: 100px;
        border-radius: 100px;
        order: 0;
    }

    .profile-dt {
        order: 1;
        margin-top: 20px;
    }

    .Hii-there-lg {
        font-size: 4vh;
    }

    .name-lg {
        font-size: 4vh;
    }

    .surname-lg {
        font-size: 4vh;
    }

    .welcome-lg {
        font-size: 2.5vh;
    }
}
